button {
  min-width: 170px;
  text-transform: uppercase;
  cursor: pointer;
  background: #b0c4de;
  border: 0;
  border-radius: 4px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  transition: background .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover {
  background: #add8e6;
}

